import { Typography } from "@mui/material";

const markdownStyles = (theme) => {
  return {
    // Use h2s instead of h1
    code: (props) => (
      <Typography
        variant="body2"
        {...props}
        sx={{ color: theme.palette.main.black }}
      />
    ),

    h3: (props) => (
      <Typography
        variant="body2"
        {...props}
        sx={{ color: theme.palette.main.black, fontWeight: "bold" }}
      />
    ),

    ul: (props) => (
      <Typography
        variant="body2"
        {...props}
        sx={{ color: theme.palette.main.black }}
      />
    ),

    li: (props) => (
      <Typography
        variant="body2"
        {...props}
        sx={{ color: theme.palette.main.black }}
      />
    ),

    p: ({ node, ...props }) => (
      <Typography
        variant="body2"
        {...props}
        sx={{ color: theme.palette.main.black }}
      />
    ),
  };
};

export default markdownStyles;
