import { getDatabase, push, ref, set } from "firebase/database";

const writeData = async (data) => {
  const db = getDatabase();
  const mailRef = ref(db, "visitors");
  const autoKeyRef = push(mailRef);
  set(autoKeyRef, data);
};

const getClientLocation = async () => {
  const response = await fetch("https://geolocation-db.com/json/");
  const data = await response.json();
  return data;
};

// Stores the user's ip and city name in database
const storeUserIp = () => {
  getClientLocation().then((data) => {
    const { city, country_name, IPv4, state } = data;
    const res = {};

    res.city = city;
    res.country = country_name;
    res.IPv4 = IPv4;
    res.state = state;

    // get current time in ms
    const date = new Date();

    // var d = new Date(1469433907836);

    // d.toLocaleString(); // expected output: "7/25/2016, 1:35:07 PM"

    // d.toLocaleDateString(); // expected output: "7/25/2016"

    // d.toDateString(); // expected output: "Mon Jul 25 2016"

    // d.toTimeString(); // expected output: "13:35:07 GMT+0530 (India Standard Time)"

    // d.toLocaleTimeString(); // expected output: "1:35:07 PM"
    res.visitedAt = date.getTime();

    writeData(res);
  });
};

export default storeUserIp;
